<template>
  <div>
    <el-menu
        class="el-menu-homepage"
        mode="horizontal"
        background-color="#1F4B62"
        text-color="#fff"
        active-text-color="#ffd04b"
        style="text-align: left">
      <el-tooltip class="item" effect="dark" content="点击回到主页" placement="bottom">
        <el-menu-item index="10" @click="backtoHomepage()" style="width: 180px;float: left">
          回到主页
        </el-menu-item>
      </el-tooltip>
      <el-menu-item index="99" @click="logout()" style="float: right">登出</el-menu-item>
    </el-menu>
    <el-container style="height: calc(100vh - 80px); border: 1px solid #eee">
      <el-aside width="200px" style="background-color: rgb(238, 241, 246)">
        <el-menu :default-openeds="['1', '3']">
          <el-submenu index="1">
            <template v-slot:title><i class="el-icon-message"></i>业务员</template>
            <el-menu-item-group>
              <el-submenu>
                <template v-slot:title>
                  订单管理
                </template>
                <el-menu-item>
                  <router-link to="/ormanage">
                    订单列表
                  </router-link>
                </el-menu-item>
                <el-menu-item>
                  <router-link to="/chargeback">
                    退单管理
                  </router-link>
                </el-menu-item>
              </el-submenu>
              <router-view/>

            </el-menu-item-group>
          </el-submenu>

        </el-menu>
      </el-aside>

      <el-container>

        <el-header style="text-align: right; font-size: 12px">
          <router-link to="/main2">回到首页</router-link>
        </el-header>

        <el-main>
          <el-input v-model="input" placeholder="订单搜索 请输入用户ID或酒店ID"></el-input>
          <el-table
              ref="filterTable"
              :data="tableData"
              style="width: 100%">
            <el-table-column type="expand">
              <template v-slot="props">
                <el-form >
                  <el-form-item label="订单状态 ">
                    <span>{{ props.row.state }}</span>
                  </el-form-item>
                  <el-form-item label="订单号">
                    <span>{{ props.row.o_id }}</span>
                  </el-form-item>
                  <el-form-item label="ID">
                    <span>{{ props.row.u_id }}</span>
                  </el-form-item>
                  <el-form-item label="入住人1">
                    <span>{{ props.row.pname1 }}</span>
                  </el-form-item>
                  <el-form-item label="电话1">
                    <span>{{ props.row.p1_tel }}</span>
                  </el-form-item>
                  <el-form-item label="入住人2">
                    <span>{{ props.row.pname2 }}</span>
                  </el-form-item>
                  <el-form-item label="电话2">
                    <span>{{ props.row.p2_tel}}</span>
                  </el-form-item>
                  <el-form-item label="酒店编号">
                    <span>{{ props.row.h_id }}</span>
                  </el-form-item>
                  <el-form-item label="订单创建时间">
                    <span>{{ props.row.Create_time }}</span>
                  </el-form-item>
                  <el-form-item label="入住时间">
                    <span>{{ props.row.checkin_time }}</span>
                  </el-form-item>
                  <el-form-item label="搬出时间">
                    <span>{{ props.row.checkout_time }}</span>
                  </el-form-item>
                  <el-form-item label="价格">
                    <span>{{ props.row.total_price}}</span>
                  </el-form-item>
                  <el-form-item label="订单评价">
                    <span>{{ props.row.review }}</span>
                  </el-form-item>
                  <el-form-item label="评分 ">
                    <span>{{ props.row.rating }}</span>
                  </el-form-item>
                </el-form>
              </template>
            </el-table-column>
            <el-table-column
                prop="state"
                label="订单状态"
                width="100"
                column-key="date"
                :filters="[{text: '待支付', value: '待支付'}, {text: '待入住', value: '待入住'}, {text: '待审批', value: '待审批'}, {text: '已退订', value: '已退订'}, {text: '已完成', value: '已完成'}]"
                :filter-method="filterHandler">
            </el-table-column>
            <el-table-column
                label="订单号"
                width="200"
                prop="o_id">
            </el-table-column>
            <el-table-column
                label="ID"
                width="200"
                prop="u_id">
            </el-table-column>
            <el-table-column
                label="酒店编号"
                prop="h_id"
                width="200">
            </el-table-column>
            <el-table-column
                label="订单创建时间"
                prop="Create_time"
                width="200">
            </el-table-column>
            <el-table-column
                label="入住时间"
                prop="checkin_time"
                width="200">
            </el-table-column>
            <el-table-column
                label="搬出时间"
                prop="checkout_time"
                width="200">
            </el-table-column>
            <el-table-column
                label="价格"
                prop="total_price"
                width="100">
            </el-table-column>
          </el-table>
        </el-main>
      </el-container>
    </el-container>

  </div>
</template>
<style>
.demo-table-expand {
  font-size: 0;
}
.demo-table-expand label {
  width: 90px;
  color: #99a9bf;
}
.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 50%;
}
</style>

<script>
export default {
// eslint-disable-next-line vue/multi-word-component-names
name: "ormanage",
  data () {
    return {
      title: '所有订单',
      tableData: [{
        o_id: '210615001',
        u_id: '2019141460096',
        h_id: '103872',
        Create_time: '2021-06-15 14:04:12',
        checkin_time: '2021-06-25 14:04:14',
        checkout_time: '2021-06-30 14:04:17',
        state: '待支付',
        total_price: '3000',
        rating:"5",
        review: '舒适，但床在摇晃',
        pname1: "何翔龙",
        pname2:"凤姐",
        p1_tel:"12124404047",
        p2_tel:"12147752774",
      }]
    }
  },
  methods:{
    backtoHomepage () {
      this.$router.push('/homepage')
    },
    filterHandler (value, row, column) {
      const property = column['property']
      return row[property] === value
    },
    logout () {
      this.$axios({
        url: '/api/userinfo/Logout',
        method: 'POST'
      }).then(
          successResponse => {
            if (successResponse.data.message) {
              this.$router.push('/login')
              // eslint-disable-next-line no-undef
              store.commit('user/SET_USER_ID', null)
              // eslint-disable-next-line no-empty
            } else {
            }
          }).catch(err => {
        console.log(err, 'err')
      })
    },
    created () {
      const self = this
      this.$axios({
        url: '/api/orderlist/listOrder',
        method: 'GET',
        params: {
          pageNo: 1,
          pageSize: 20
        }
      }).then(
          successResponse => {
            if (successResponse.data.data) {
              self.tableData = successResponse.data.data
              // console.log(this.session)
              // eslint-disable-next-line no-empty
            } else {
            }
          }).catch(err => {
        console.log(err, 'err')
      })
    }
  }
}
</script>

<style scoped>

</style>
